.shopcategory-banner{
    display: block;
    margin: 30px auto;
    width: 82%;
}
.shopcategory-indexsort{
display: flex;
margin: 0px 170px;
justify-content: space-between;
align-items: center;
}
.shopcategory-sort{
padding: 10px 20px;
border-radius: 40px;
border: 1px solid #888;
}
.shopcategory-indexsort p span{
font-weight: 600;
}
.shopcategory-products{
    margin: 20px 170px;
    display: grid;
    row-gap: 80px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    
}
.shopcategory-loadmore{
  display: flex;
  align-items: center;
  color: #787878;
  background-color: #ededed;
  font-size: 18px;
  margin: 150px auto;
  border-radius: 75px;
  justify-content: center;
  width: 233px;
  height: 69px;
  font-weight: 500;
}
