.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}

.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;

}
.nav-logo {
    gap: 10px;
    display: flex;
    align-items: center;

}
.nav-menu{
    color: #626262;
    font-size: 20px;
    font-weight: 500;
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
}
.nav-menu li{
    cursor: pointer;
    justify-items: center;
    flex-direction: column;
    gap: 3px;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    border-radius: 75px;
    background-color: white;
    cursor: pointer;
}
.nav-login-cart button:active{
   background: #f3f3f3;
}
.nav-cart-count{
   width: 22px;
   height: 22px;
   display: flex;
   align-items: center;
   justify-content: center;
   margin-top: -35px;
   margin-left: -55px;
   border-radius: 11px;
   font-size: 14px;
   background-color: red;
   color:white;


}

